.nav {
  margin: 64px 40px 0 0;
  min-width: 100px;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}

.nav__item {
  a {
    display: block;
    padding: 15px 20px 15px 0;
    color: $body-color;
    text-decoration: none;
    font-family: $font-family;
    font-size: 16px;
    border-bottom: 1px solid $border-color;
  }
}

.nav__item:last-of-type {
  a {
    border-bottom: none;
  }
}

.nav__item--active {
  a {
    font-weight: 600;
    color: $primary-text;
  }
}
