.button {
  cursor: pointer;
  display: inline-block;
  border-radius: 3px;
  padding: 10px 20px;
  font-family: $font-family;
  font-weight: 500;
  font-size: 16px;
  line-height: 1;
  color: $body-color;
  text-decoration: none;

  &:focus {
    outline: none;
  }
}

.button--primary {
  background-color: $priamry;
  border: 1px solid $priamry;
  color: $white;
  &:focus {
    $bg: darken($priamry, 5%);
    background-color: $bg;
    box-shadow: 0 0 0 0.2rem rgba($priamry, 0.2);
  }
}

.button--danger {
  background-color: $danger;
  border: 1px solid $danger;
  color: $white;
  &:focus {
    $bg: darken($danger, 5%);
    background-color: $bg;
    box-shadow: 0 0 0 0.2rem rgba($danger, 0.2);
  }
}
