.table {
  display: grid;
  background-color: $white;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
}

.table__row {
  display: flex;
  align-items: center;
  padding: 15px 15px;
  border-bottom: 1px solid $border-color;
}

.table__text {
  font-family: $font-family;
  font-weight: 400;
  font-size: 18px;
  flex: 1 0 auto;
  color: $body-color;
  line-height: 1;
  text-decoration: none;
  &:hover {
    color: $primary-text;
  }
}

.table__button {
  flex: 0 0 20px;
  width: 20px;
  height: 20px;
  background-position: center;
  background-size: cover;
}

