body {
  margin: 0;
}

*,
::before,
::after {
  box-sizing: border-box;
}

.library {
  background-color: #f5f5f5;
}

.nav {
  margin: 64px 40px 0 0;
  min-width: 100px;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}

.nav__item a {
  display: block;
  padding: 15px 20px 15px 0;
  color: #484848;
  text-decoration: none;
  font-family: Arial, Hevletica, sans-serif;
  font-size: 16px;
  border-bottom: 1px solid #ededed;
}

.nav__item:last-of-type a {
  border-bottom: none;
}

.nav__item--active a {
  font-weight: 600;
  color: #24292f;
}

.section {
  width: 600px;
}

.section__title {
  color: #484848;
  font-family: Arial, Hevletica, sans-serif;
  font-weight: 600;
  font-size: 30px;
  margin: 0;
  margin-bottom: 30px;
}

.content {
  margin-top: 40px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.table {
  display: grid;
  background-color: #fff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
}

.table__row {
  display: flex;
  align-items: center;
  padding: 15px 15px;
  border-bottom: 1px solid #ededed;
}

.table__text {
  font-family: Arial, Hevletica, sans-serif;
  font-weight: 400;
  font-size: 18px;
  flex: 1 0 auto;
  color: #484848;
  line-height: 1;
  text-decoration: none;
}
.table__text:hover {
  color: #24292f;
}

.table__button {
  flex: 0 0 20px;
  width: 20px;
  height: 20px;
  background-position: center;
  background-size: cover;
}

